
<template>
  <div>
    <section class="content-header">
      <h1>Quản lý chức danh công việc</h1>
      <ol class="breadcrumb">
        <li>
          <a><i class="fa fa-dashboard"></i>Trang chủ</a>
        </li>
        <li><a>Danh sách chức danh công việc</a></li>
      </ol>
    </section>
    <section class="panel panel-inverse">
      <div class="row">
        <div class="panel-body">
          <button class="btn btn-primary m-r-5 m-b-5" @click="showModal">
            Thêm chức danh công việc
          </button>
        </div>

        <table class="table">
          <thead>
            <tr>
              <th width="25px">STT</th>
              <th>Tên chức danh công việc</th>
              <!-- <th>Username</th> -->
              <!-- <th>Email</th>
              <th>Điện thoại</th> -->
              <!-- <th>Phân quyền</th> -->
              <th width="15px" class="functionicon"></th>
              <th width="15px" class="functionicon"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in items" :key="index">
              <td>{{ (currentPage - 1) * 20 + index + 1 }}</td>
              <td>{{ item.name }}</td>
              <!-- <td>{{ item.username }}</td> -->
              <!-- <td>{{ item.email }}</td> -->
              <!-- <td>{{ item.username }}</td> -->
              <!-- <td>{{ displayUserType(item.type) }}</td> -->
              <td>
                <span>
                  <a href="#" @click="showisModalEditVisible(item)">
                    <i class="fa fa-edit"></i>
                  </a>
                </span>
              </td>
              <td>
                <a href="#" @click="dellItem(item._id, item.name)">
                  <i class="fa fa-trash"></i>
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        id="example2_paginate"
        class="dataTables_paginate paging_simple_numbers"
      >
        <paginate
          v-model="currentPage"
          :page-count="Math.ceil(totalRows / 20)"
          :page-range="3"
          :margin-pages="2"
          :click-handler="clickCallback"
          :prev-text="'Prev'"
          :next-text="'Next'"
          :container-class="'pagination'"
          :page-class="'page-item'"
        >
        </paginate>
      </div>
      <adduser
        @inputData="updateMessage"
        v-show="isModalVisible"
        @close="closeModal"
      />
      <edituser
        :editid="editid"
        v-show="isModalEditVisible"
        @close="closeEditModal"
      />
    </section>
    <vue-confirm-dialog></vue-confirm-dialog>
  </div>
</template>
<script>
import adduser from "@/components/schools/adduser";
import edituser from "@/components/schools/edituser";
const { BASE_URL } = require("../../utils/config");
export default {
  data() {
    return {
      isModalVisible: false,
      isModalEditVisible: false,
      schools: [],
      items: [],
      totalRows: 1,
      perPage: 20,
      currentPage: Number(this.$route.query.page),
      editid: "",
    };
  },
  components: {
    adduser,
    edituser,
  },
  methods: {
    showModal() {
      this.isModalVisible = true;
    },
    showisModalEditVisible(id) {
      this.editid = id;
      this.isModalEditVisible = true;
    },
    closeModal() {
      this.isModalVisible = false;
    },
    displayUserType(id) {
      switch (id) {
        case 1:
          return "Quản trị viên";
          break;
        case 2:
          return "Người quản lý";
          break;
        default:
          return "Nhân viên";
      }
    },
    closeEditModal() {
      this.isModalEditVisible = false;
    },
    clickCallback(pageNum) {
      this.items = this.schools.filter((item, index) => {
        if (
          (pageNum - 1) * this.perPage <= index &&
          index < pageNum * this.perPage
        ) {
          return true;
        }
        return false;
      });
    },
    updateMessage(variable) {
      this.items = variable;
    },
    dellItem(id, name) {
      this.$confirm({
        message: "Bạn có muốn xóa " + name,
        button: {
          yes: "Đồng ý",
        },
        callback: (confirm) => {
          if (confirm) {
            this.$http
              .post(
                "school/delete",
                {
                  id: id,
                },
                {
                  headers: {
                    Authorization: `Basic ${localStorage.getItem("token")}`,
                  },
                }
              )
              .then((response) => {
                this.$http
                  .get(`${BASE_URL}/school/getall`, {
                    headers: {
                      Authorization: `Basic ${localStorage.getItem("token")}`,
                    },
                  })
                  .then((response) => {
                    this.schools = response.data;
                    this.totalRows = response.data.length;
                    this.items = this.schools.filter((item, index) => {
                      if (
                        (this.currentPage - 1) * this.perPage <= index &&
                        index < this.currentPage * this.perPage
                      ) {
                        return true;
                      }
                      return false;
                    });
                  });
              })
              .catch(function (error) {
                console.error(error.response);
              });
          }
        },
      });
    },
  },

  created() {
    this.$http
      .get(`${BASE_URL}/school/getall`, {
        headers: { Authorization: `Basic ${localStorage.getItem("token")}` },
      })
      .then((response) => {
        this.schools = response.data;
        this.totalRows = response.data.length;
        if (this.$route.query.page === undefined) {
          this.currentPage = 1;
        }
        this.items = this.schools.filter((item, index) => {
          if (
            (this.currentPage - 1) * this.perPage <= index &&
            index < this.currentPage * this.perPage
          ) {
            return true;
          }
          return false;
        });
      });
  },
};
</script>


